.TodoLoading {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    margin-left: 120px;
    box-shadow: 2px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    width: calc(100% - 240px);
}

.TodoLoading-p {
    font-family: Calibri, serif;
    text-align: center;
    padding: 12px;
    border-color: #121225;
    border-width: 5px;
    background-color: lightgrey;
    margin: 24px 0 24px 24px;
    width: calc(100% - 240px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    border-radius: 10px;

}
.TodoLoading-p--complete {
    text-decoration: line-through;
}

.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    margin-left: 120px;
    box-shadow: 2px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    padding: 12px 0;
    width: calc(100% - 240px);
}

.LoadingTodo-text {
    font-family: Calibri, serif;
    text-align: center;
    padding: 12px;
    border-color: #121225;
    border-width: 5px;
    background-color: lightgrey;
    margin: 24px 0 24px 24px;
    width: calc(100% - 240px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    border-radius: 10px;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    height: 48px;
    width: 48px;
}
.LoadingTodo-completeIcon {
    position: absolute;
    left: 24px;
}
.LoadingTodo-deleteIcon {
    position: absolute;
    right: 24px;
}


.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    background: linear-gradient(45deg, rgba(250,250,250,1), rgb(200, 199, 199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0 50%;
    }
}